    border: 1px solid red;
} */

.header {
    position: fixed;
    left: 0;
    right: 0;
    min-height: 150px;
    padding-top: 40px;
    background-color: rgba(0, 0, 0, 0);
    background-image: linear-gradient(var(--color-light-green), rgba(0, 0, 0, 0));
    z-index: 10;
}

.header__wrapper_menu {
    display: flex;
    justify-content: space-between;
}

.header__menu-btn {
    position: relative;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    column-gap: 16px;
    min-width: 115px;
    min-height: 40px;
    color: var(--color-light-blue);
    border-radius: var(--border-radius);
    cursor: pointer;
    text-transform: uppercase;
    transition: .9s ease;
}


.header__menu-btn::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background-color: var(--color-light-blue);
    opacity: 15%;
    transition: all .6s ease;
    border-radius: var(--border-radius);
}

.header__menu-btn:hover::before {
    opacity: 0;
    transform: scale(0.6, 0.6);
}

.header__menu-btn::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    opacity: 0;
    transition: all .6s ease;
    border: 1px solid var(--color-light-blue);
    transform: scale(1.2, 1.2);
    border-radius: 10px;
}

.header__menu-btn:hover::after {
    opacity: 1;
    transform: scale(1, 1);
}

.header__menu-btn:active {
    background-color: var(--color-light-green);
}

.hamburger {
    display: flex;
    flex-direction: column;
    row-gap: 4px;
}

.bar {
    display: block;
    width: 18px;
    height: 2px;
    background-color: var(--color-light-blue);
    border-radius: var(--border-radius);
}

.bar:nth-child(2) {
    transform: translateX(5px);
}

.header__menu {
    position: relative;
    font-size: 15px;
    font-weight: 400;
    text-transform: uppercase;
}

.header__link_contact {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 43px;
    font-family: "Inter 18pt", serif;
    text-transform: uppercase;
    font-size: 14px;
    color: #253466;
    color: var(--color-dark-blue, #253466);
}

.header__link_contact:hover {
    color: var(--color-light-green);
}

.logo {
    width: 184px;
    height: 89px;
    font-weight: 600;
    font-size: 18px;
    text-transform: uppercase;
    text-align: center;
    color: #fff;
    color: var(--color-white, #fff);
}

.mr-67 {
    margin-right: 67px;
}

.popup-fade::before {
    content: "";
    background: #000;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    opacity: .8;
    z-index: 1;
}

.popup {
    position: fixed;
    display: flex;
    top: 0;
    width: 0;
    height: 100%;
    transition: width 1s ease;
    height: 100%;
    transition: width 1s ease;
    background: #fff;
    z-index: 1;
    overflow-y: auto;
    overflow-x: hidden;
    background-color: var(--color-light-blue);
}

.popup__menu-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 0 15px 20px 0px;
    margin-left: 70px;
    text-align: left;
    width: 35%;
    min-width: 450px;
}

.title-menu {
    margin-top: 64px;
}

.title-menu span {
    font-family: "Saira", sans-serif;
    display: block;
    line-height: 1;
    font-style: italic;
    font-weight: 800;
    font-size: 48px;
    text-transform: uppercase;
    color: var(--color-white);
}

.title-menu span:nth-child(2) {
    margin-left: 80px;
}

.nav {
    margin-top: 50px;
}

.header__list {
    display: flex;
    flex-direction: column;
    overflow: auto;
}

.header__list-item a {
    font-family: "Overpass", sans-serif;
    display: block;
    font-weight: 400;
    font-size: 30px;
    text-transform: uppercase;
    color: var(--color-blue);
    transition: var(--transition-duration);
}

.header__list-item a:hover {
    color: var(--color-pink);
}

.header__menu-popup-button {
    width: 200px;
    height: 40px;
    margin-top: 30px;
    margin-right: 50px;
    font-size: 20px;
    font-weight: 400;
}

.menu-close {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    left: 400px;
    top: 15px;
    width: 100px;
    aspect-ratio: 1;
    border: 1px solid var(--color-blue);
    border-radius: 100%;
    cursor: pointer;
}

.menu-close span {
    display: inline-block;
    width: 50px;
    height: 3px;
    background-color: var(--color-blue);
}

.menu-close span:nth-child(1) {
    transform: rotate(45deg) translate(2px);
}

.menu-close span:nth-child(2) {
    transform: rotate(-45deg) translate(2px);
}

.menu-close:hover span {
    background-color: var(--color-pink);
    transition: var(--transition-duration);
}

.menu-close:hover {
    border-color: var(--color-pink);
    transition: var(--transition-duration);
}

.image-container.show {
    display: flex;
    width: 100%;
    height: 100vh;
    transition: width 1s ease;
}

.popup__img {
    width: 100%;
    height: 100vh;
    object-fit: cover;
}

.open {
    left: 0;
    width: 60%;
    transition: width 1s ease;
}

.z0 {
    z-index: 0;
}

.pt {
    padding-top: 7px;
}

.text {
    color: var(--color-blue);
}
