    margin-left: auto;
    margin-right: auto;
}

.section-title {
    font-family: var(--font-secondary);
    font-weight: 600;
    font-size: 32px;
    text-transform: capitalize;
    color: var(--color-dark-blue);
}

.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
}

.preloader {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: hidden;
    background: #e0e0e0;
    z-index: 100;
}

.preloader__image {
    position: relative;
    top: 50%;
    left: 50%;
    width: 70px;
    height: 70px;
    margin-top: -35px;
    margin-left: -35px;
    text-align: center;
    animation: preloader-rotate 2s infinite linear
}

@keyframes preloader-rotate {
    100% {
        transform: rotate(360deg)
    }
}

.loaded_hiding .preloader {
    transition: .3s opacity;
    opacity: 0
}

.loaded .preloader {
    display: none
}

.menu-button {
    position: relative;
    text-transform: uppercase;
    text-align: center;
    color: var(--color-light-blue);
    border-radius: var(--border-radius);
    background-color: var(--color-light-green);
}

.menu-button::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background-color: var(--color-light-blue);
    opacity: 15%;
    transition: all .6s ease;
    border-radius: var(--border-radius);
}

.menu-button:hover::before {
    opacity: 0;
    transform: scale(0.6, 0.6);
}

.menu-button::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    opacity: 0;
    transition: all .6s ease;
    border: 1px solid var(--color-light-blue);
    transform: scale(1.2, 1.2);
    border-radius: 10px;
}

.menu-button:hover::after {
    opacity: 1;
    transform: scale(1, 1);
}

.menu-button:active {
    background-color: var(--color-light-green);
}

.phone {
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;

    bottom: 50px;
    right: 100px;
    z-index: 11;
}

.phone svg {
    width: 50px;
    height: 50px;
}

.phone:before,
.phone:after {
    content: " ";
    display: block;
    position: absolute;
    border: 50%;
    border: 1px solid var(--color-pink);
    left: -20px;
    right: -20px;
    top: -20px;
    bottom: -20px;
    border-radius: 50%;
}

.phone:before,
.phone:after {
    animation: animate 1.5s linear infinite
}


@keyframes animate {

    0% {
        transform: scale(0.5);
        opacity: 0;
    }

    50% {
        opacity: 1;
    }

    100% {
        transform: scale(1.2);
        opacity: 0;
    }
}


.popup_menu_overflow {
    overflow: auto;
}

.btn-style {
    position: relative;
    border-radius: var(--border-radius);
    color: var(--light-blue);
}

.btn-style::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background-color: var(--light-blue);
    opacity: 15%;
    transition: 0.6s;
    border-radius: var(--border-radius);
}

.btn-style:hover::before {
    opacity: 0;
    transform: scale(0.6, 0.6);
}

.btn-style::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    opacity: 0;
    transition: 0.6s;
    border: 1px solid var(--light-blue);
    transform: scale(1.2, 1.2);
    border-radius: var(--border-radius);
}

.btn-style:active {
    background-color: var(--dark-green);
}

.btn-style:hover::after {
    opacity: 1;
    transform: scale(1, 1);
}

.btn-style:focus {
    outline: none;
}

.card-mobile-parameters {
    min-height: 320px;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    align-content: center;
    justify-content: center;
    text-align: center;
}

