    margin-inline: auto;
}

.no-scroll {
    overflow: hidden;
}

.dp-fl {
    display: flex;
}

.grid {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 40px;
    margin: 0 40px;
}

.section-parameters {
    max-width: 1140px;
    margin: 0 auto;
}

.bg {
    background: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-size: cover;
    height: 100vh;
}

.btn-reset {
    border: none;
    cursor: pointer;
    font-family: "Overpass", "sans-serif";
    background: none;
